<template>
	<el-container v-loading="loading">
		<div class="rise x add">
			<div class="title">{{title}}</div>
			<div class="content">
				<div class="item" v-for="(item, index) in data" :key="index">
					<span>{{item.name}}</span>
					<el-input v-if="item.module === 'count'" v-model="count" :placeholder="item.placeholder"/>
					<el-date-picker v-if="item.module === 'month'" v-model="month" :placeholder="item.placeholder" type="date"/>
				</div>
			</div>
			<div class="btn-list">
				<el-button @click="cancle()" class="btn">取消</el-button>
				<el-button @click="save()" class="btn">保存</el-button>
			</div>
		</div>
	</el-container>
</template>

<script>
import queryString from "querystring";
import { DateDay } from "utils/formData";
import Picupload from "components/picupload";
const titles = ["新建", "编辑"];
const defaultV = [
  { name: "单人月均额：", placeholder: "请输入单人月均额", module: "count" },
  {
    name: "日期：",
    placeholder: "请选择日期",
    module: "month",
    type: "date"
  },
];

export default {
  name: "add",
  data() {
    return {
      title: "",
      id: "",
      count: "",
      month: "",
      type: "",
      data: defaultV,
      loading: false
    };
  },
  mounted() {
    this.dataInit();
  },
  methods: {
    dataInit() {
      const { type, id } = this.$route.query;
      this.type = type;
      this.id = id;
      if (type !== "edit") return (this.title = titles[0]);
      this.title = titles[1];
      this.detailAjax();
    },
    editInit(res) {
      const { count, month } = res.data;
      this.count = count;
      this.month = month;
    },
    detailAjax() {
      //查看详情
      this.loading = true;
      this.api.get(`/scoreLimit/${this.id}`).then(res => {
          this.loading = false;
          this.editInit(res);
        })
        .catch(err => {
          console.log(err);
          this.loading = false;
          this.$message(err.message);
        });
    },
    editAjax() {
      const params = {
        month: DateDay(this.month),
        count: this.count
      };
      const axiosType = ["post", "put"];
      let type = axiosType[0];
      if (this.type === "edit") {
        params.id = this.id;
        type = axiosType[1];
      }
      this.loading = true;
      this.api[type](`/scoreLimit`, params, "form").then(res => {
          this.loading = false;
          this.$router.go(-1);
        })
        .catch(err => {
          this.loading = false;

          console.log(err);
          this.$message(err.message);
        });
    },
    save() {
      this.editAjax();
    },
    cancle() {
      //取消
      this.$router.go(-1);
    }
  }
};
</script>
 
<style lang="less" scoped>
</style>
 